'use strict';
var el = angular.element;
angular.module('components').directive('focusMe', [function() {
    return {
        scope: {
            trigger: '=focusMe'
        },
        link: function(scope, element) {
            scope.$watch('trigger', function(value) {
                if (value === true) {
                    element[0].focus();
                    scope.trigger = false;
                }
            });
        }
    };
}]).directive('memberSearch', [function() {
    var directive = {
        restrict: 'E',
        templateUrl: 'app/components/search/search.html',
        scope: true
    };
    return directive;
}]).controller('SearchController', [
    '$scope',
    '$window',
    '$document',
    '$element',
    'enrollmentService',
    '$injector',
    'formatValidationService',
    '$rootScope',
    '$state',
    'dataBusService',
    function($scope, $window, $document, $element, enrollmentService, $injector, formatValidationService, $rootScope,$state,dataBusService) {
        var vm = this;
        var contract = el($element[0].querySelector('[data-radio="one"]'));
        var member = el($element[0].querySelector('[data-radio="two"]'));
        vm.isVisible = false;
        vm.validationError = false;
        vm.searchContent = '';
        vm.criteria = ['Contract ID, CSA/CSF, SSN', 'Member ID'];
        vm.criterion = vm.criteria[0];
        vm.contractSummary = {};

        $scope.$watch('search.searchContent', function(newValue) {
            vm.validationError = false;
            $rootScope.contractNotFound = false;
            $rootScope.defaultApiError = false;
            if (newValue && newValue.length > 9) {
                vm.searchContent = newValue.substring(0, 9);
            }
        });

        vm.toggle = function() {
            vm.isVisible = true;
            vm.criterion = vm.criteria[0];
            if (vm.isVisible == true) {
                $window.onclick = function(event) {
                    vm.isVisible = false;
                    $scope.$apply();
                };
            }
            el(member).removeClass('is-checked');
            el(contract).addClass('is-checked');
        };

        vm.clearSearch = function() {
            $rootScope.contractNotFound = false;
            vm.isVisible = false;
            $rootScope.defaultApiError = false;
            vm.validationError = false;
            vm.toggle();
            vm.searchContent = '';
        };

        vm.keyPressEvent = function($event) {
            var keyCode = $event.which || $event.keyCode;
            if (keyCode === 13) {
                vm.submitSearch(vm.searchContent);
            }
        };

        vm.submitSearch = function(searchContent) {
            searchContent = vm.searchContent;
            dataBusService.currentContractID = searchContent;
            vm.isVisible = false;
            $rootScope.contractNotFound = false;
            $rootScope.defaultApiError = false;
            if (formatValidationService.formatValidation(searchContent)) {
                vm.validationError = false;
                vm.toggle();
                enrollmentService.getContract(vm.searchContent).then(function(data) {
                    enrollmentService.contractSummary = data.contractSummary;
                    enrollmentService.prepForBroadcast(searchContent);
                    $state.go('enrollment');
                });
            } else {
                vm.validationError = true;
            }
        };
        vm.hideOthers = function(event) {
            event.stopPropagation();
            $rootScope.$broadcast('searchBarIsOpen');
        }
        $scope.$on('notificationIsOpen', function() {
            vm.isVisible = false;
        })
        $scope.$on('enrollmentMemberRosterIsOpen', function() {
            vm.isVisible = false;
        })
    }
]);

/**
 * @desc navbar directive and controller for the member 360 application
 * @var 'use-sctrict' globally enforce js strict mode for this function
 */
(function() {
	'use strict';

	/**
	 * @desc $log is for the angular logging service, comment out if not logging
	 * for this component
	 * @var '$log' global log service
	 */
	// var $log = angular.injector(['ng']).get('$log');

	/**
	 * @desc register the navbar component with the member 360 components module
	 * @param string 'components' the angular module to register to
	 * @param string 'memberNavbar' the angular directive name
	 */

	/**
		* @desc 'memberNavbar' angular directive used by @ng-inject
		* @param function 'memberNavbar' function to pass directive to @ng-inject
		* @return 'memberNavbar' return the directive to @ng-inject
		*/
	angular.module('components').directive('memberNavbar', [function() {
			/**
		 * @desc the angular directive variable used by @ng-inject
		 * @type object 'directive' used by @ng-inject
		 */
			var directive = {
				restrict: 'E',
				templateUrl: 'app/components/navbar/navbar.html',
				scope: true
			};
			return directive;
		}
	])

	/**
		 * @desc 'NavbarController' angular controller used by @ng-inject
		 * @param function 'NavbarController' declare the controller
		 * @param service '$rootScope' angular rootScope service
		 * @param service '$window ' angular window service
		 * @return 'NavbarController' to be used by @ng-inject
		 */
		.controller('NavbarController', [
		'$rootScope',
		'$window',
		function($rootScope, $window) {
			function load_materialjs() {
				var updateCallback = $rootScope.$on('$viewContentLoaded', function() {
					var componentHandler = $window.componentHandler;
					componentHandler.upgradeAllRegistered();
				});
				$rootScope.$on('$destroy', updateCallback)
			}
			load_materialjs();
		}
	]);

})();

