/**
 * @desc dock directive and controller for member 360
 * @var 'use-sctrict' globally enforce js strict mode for this function
 */
(function() {
    'use strict';
    /**
     * @desc $log is for the angular logging service
     * @var '$log' global log service
     */
    //var $log = angular.injector(['ng']).get('$log');
    /**
     * @desc register the dock component with the member 360 components module
     * @param string 'components' the angular module to register to
     * @param string 'memberDock' the angular directive name
     */
    /**
     * @desc 'memberDock' angular directive used by @ng-inject
     * @param function 'memberDock' function to pass directive to @ng-inject
     * @return 'memberDock' return the directive to @ng-inject
     */
    angular.module('components').directive('memberDock', [function() {
            /**
             * @desc the angular directive variable used by @ng-inject
             * @type object 'directive' used by @ng-inject
             */
            var directive = {
                restrict: 'E',
                templateUrl: 'app/components/dock/dock.html',
                scope: true
            };
            return directive;
        }])
        /**
         * @desc 'DockController' angular controller used by @ng-inject
         * @param function 'DockController' declare the controller
         * @param '$scope' current scope of this view
         * @return 'DockController' to be used by @ng-inject
         */
        .controller('DockController', ['$state', 'enrollmentService', '$scope', function($state, enrollmentService, $scope) {
            var vm = this;
            vm.dashboardLinkActive = true;
            vm.enrollmentLinkActive = false;

            vm.dashboardLink = function() {
                if (!vm.dashboardLinkActive) {
                    vm.dashboardLinkActive = true;
                    vm.enrollmentLinkActive = false;
                }
                $state.go('dashboard');
            }

            vm.enrollmentLink = function() {
                if (!vm.enrollmentLinkActive && enrollmentService.hasEnrollmentInfo) {
                    vm.dashboardLinkActive = false;
                    vm.enrollmentLinkActive = true;
                    $state.go('enrollment');
                }
            }

            $scope.$on('broadcastSearchContent', function() {
                vm.dashboardLinkActive = false;
                vm.enrollmentLinkActive = true;
            });
            $scope.$on('broadcastSearchFail', function() {
                vm.dashboardLinkActive = true;
                vm.enrollmentLinkActive = false;
            });
        }]);
})();

