/**
 * @desc breadcrumb directive and controller for the member 360 application
 * @var 'use-sctrict' globally enforce js strict mode for this function
 */
(function() {
    'use strict';
    /**
     * @desc $log is for the angular logging service, comment out if not logging
     * for this component
     * @var '$log' global log service
     */
    var $log = angular.injector(['ng']).get('$log');
    /**
     * @desc 'memberBreadcrumb' angular directive used by @ng-inject
     * @param function 'memberBreadcrumb' function to pass directive to @ng-inject
     * @return 'memberBreadcrumb' return the directive to @ng-inject
     */
    angular.module('components').directive('memberBreadcrumb', [function() {
            /**
             * @desc the angular directive variable used by @ng-inject
             * @type object 'directive' used by @ng-inject
             */
            var directive = {
                restrict: 'E',
                templateUrl: 'app/components/breadcrumb/breadcrumb.html',
                transclude: true,
                scope: true
            };
            return directive;
        }])
        /**
         * @desc 'BreadcrumbController' angular controller used by @ng-inject
         * @param function 'BreadcrumbController' declare the controller
         * @param '$location' this is the current location of the application
         * @return 'BreadcrumbController' to be used by @ng-inject
         */
        .controller('BreadcrumbController', [
            '$location',
            function($location) {
                var vm = this;
                var currentBreadCrumb = $location.path();
                $log.log(currentBreadCrumb);
                vm.isVisible = false;
                vm.breadCrumb = currentBreadCrumb;
                /**
                 * TODO - finish up for location path trim off
                 * the forward slash for current breadcrumb display
                 */
                if (currentBreadCrumb.charAt(0) === '/') {
                    currentBreadCrumb = currentBreadCrumb.slice(1);
                    vm.breadCrumb = currentBreadCrumb;
                }
                
                vm.toggle = function() {
                    vm.isVisible = !vm.isVisible;
                }
            }
        ]);
})();

/**
 * Wellness Table Service
 * Author: Lu Zhang
 */
'use strict';

angular.module('wellnesstable', []);
angular.module('wellnesstable').factory('wellnesstableService', [
  '$http',
  '$q',
  '$rootScope',
  'enrollmentService',
  '$filter',
  function ($http, $q, $rootScope, enrollmentService, $filter) {
    var vm = this;
    vm.currentMemberID;
    vm.currentContractID;
    vm.currentWellnesstableInfo;
    vm.currentMemberFirstName;

    vm.getWellnesstableInfo = function (memberId, contractId) {
      var deferred = $q.defer();
      $http.get('app/wellnesstable/wellnessTable.json').success(function (data) {
        deferred.resolve(data);
      }).error(function (err) {
        deferred.reject(err);
      });
      return deferred.promise;
    };

    vm.postWellnesstableInfo = function (memberId, contractId) {
      var formData = "memberId=" + memberId;
      var deferred = $q.defer();
      $http.post('api/wellnessprograms/member', formData).success(function (data) {
        deferred.resolve(data);
      }).error(function (err) {
        deferred.reject(err);
      });
      return deferred.promise;
    };

    return {
      getWellnesstableInfo: vm.getWellnesstableInfo,
      postWellnesstableInfo: vm.postWellnesstableInfo,
      currentMemberID: vm.currentMemberID,
      currentContractID: vm.currentContractID,
      currentMemberFirstName: vm.currentMemberFirstName,
      currentWellnesstableInfo: vm.currentWellnesstableInfo
    }
  }
]);

