angular.module('enrollment').factory('enrollmentService', [
	'$http',
	'$q',
	'$rootScope',
	'$state',
	'errorHandleService',
	function($http, $q, $rootScope,$state,errorHandleService) {
		var vm = this;
		var content = '';
		vm.contractSummary;
		vm.hasEnrollmentInfo;
		vm.currentIDInfo;
		vm.error;
		vm.prepForBroadcast = function(msg) {
			this.content = msg;
			vm.broadcastItem();
		};
		vm.broadcastItem = function() {
			$rootScope.$broadcast('broadcastSearchContent');
		};
		vm.getContract = function(id) {
			var deferred = $q.defer();
			$http.get('app/enrollment/' + id + '.json').success(function(data) {
				deferred.resolve(data);
				vm.contractSummary = data;
				$log.log(data);
				vm.currentContractId = id;
			}).error(function(err) {
				/*$log.log('error retrieving...');*/
				errorHandleService.notFoundErrorHandler(err);
				if(!vm.hasEnrollmentInfo) {
					$rootScope.$broadcast('broadcastSearchFail');
					//$state.go('dashboard');
				}
				deferred.reject(err);
			});
			return deferred.promise;
		};
		vm.postContract = function(id) {
			var formData = "contractId=" + id;
			var deferred = $q.defer();
			$http.post('api/contract/summary', formData).success(function(data) {
				deferred.resolve(data);
				/*$log.log(data);*/
			}).error(function(err) {
				/*$log.log('error retrieving...' + $rootScope.contractNotFound);*/
				errorHandleService.notFoundErrorHandler(err);
				if(!vm.hasEnrollmentInfo) {
					$rootScope.$broadcast('broadcastSearchFail');
					//$state.go('dashboard');
				}
				deferred.reject(err);
			});
			return deferred.promise;
		};
		return {
      hasEnrollmentInfo: vm.hasEnrollmentInfo,
			getContract: vm.getContract,
			postContract: vm.postContract,
			content: content,
			prepForBroadcast: vm.prepForBroadcast,
			broadcastItem: vm.broadcastItem,
			contractSummary: vm.contractSummary,
			currentIDInfo: vm.currentIDInfo
		}
	}
]);

/**
 * Enrollment Filter
 * Author: Lu Zhang
 */
(function() {
	'use strict';
	// var $log = angular.injector(['ng']).get('$log');
	angular.module('enrollment').filter('camelCase', [function() {
			return function(input) {
				input = input || '';
				return input.replace(/\w\S*/g, function(txt) {
					return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
				});
			};
		}
	]).filter('tel', [function() {
			return function(tel) {
				if (!tel) {
					return '';
				}
				var value = tel.toString().trim().replace(/^\+/, '');
				if (value.match(/[^0-9]/)) {
					return tel;
				}
				var country,
					city,
					number;
				switch (value.length) {
					case 10: // +1PPP####### -> C (PPP) ###-####
						country = 1;
						city = value.slice(0, 3);
						number = value.slice(3);
						break;
					case 11: // +CPPP####### -> CCC (PP) ###-####
						country = value[0];
						city = value.slice(1, 4);
						number = value.slice(4);
						break;
					case 12: // +CCCPP####### -> CCC (PP) ###-####
						country = value.slice(0, 3);
						city = value.slice(3, 5);
						number = value.slice(5);
						break;
					default:
						return tel;
				}
				if (country == 1) {
					country = "";
				}
				number = number.slice(0, 3) + '-' + number.slice(3);
				return (country + " (" + city + ") " + number).trim();
			};
		}
	]).filter("ssn", [function() {
			return function(value) {
				value = value.toString();
				if (value) {
					if (value.length >= 9) {
						return value.substr(0, 3) + "-" + value.substr(3, 2) + "-" + value.substr(5);
					} else if (value.length >= 5) {
						return value.substr(0, 3) + "-" + value.substr(3, 2) + "-" + value.substr(5);
					} else if (value.length > 3) {
						return value.substr(0, 3) + "-" + value.substr(3);
					}
				}
				return value;
			};
		}
	]).filter("zipcode", [function() {
			return function(zipcode) {
				zipcode = zipcode || '';
				zipcode = zipcode + "";
				if (zipcode.length >= 9) {
					return zipcode.substr(0, 5) + "-" + zipcode.substr(5);
				} else if (zipcode == undefined) {
					return null;
				} else {
					return zipcode;
				}
			};
		}
	]).filter("prependZeroFixedLen", [function() { //Filter to prepend zeros for the length provided
			return function(value, length) {
				var numValue = parseInt(value, 10);
				length = parseInt(length, 10);
				if (isNaN(numValue) || isNaN(length)) {
					return value;
				}
				numValue = '' + numValue;
				while (numValue.length < length) {
					numValue = '0' + numValue;
				}
				return numValue;
			}
		}
	]).filter("formatContractId", function($filter) { //Filter to prepend zeros for the length provided
		return function(value) {
			if(angular.isDefined(value)) {
				return 'R'+$filter('prependZeroFixedLen')(value, 8);
			}
			return value;
		}
	}).filter("bracketUppercase",[function(){
			return function(str){
				if(angular.isDefined(str)) {
					if(str.indexOf("(") != -1){
						var n = str.indexOf('(')+1;
						var m = str.indexOf(')');
						var temp = str.substring(n,m);
						return str.replace(temp,temp.toUpperCase());
					}
				}
				return str;
			}
		}
	]);
})();

